    /**
     * Converts a string to function
     * @param string
     * @returns {function|object} Function or object {error: "error text"}
     * @private
     *
     */
    functionFromString(string: string): Function|{error: string} {
        var _this = this;
        var func;
        var error = {error: ''};
        var fn = string.trim();
        if(fn.indexOf("{")==-1 || fn.indexOf("function")!==0 || fn.indexOf("(")==-1){
            return {error: "MapSVG user function error: no function body."};
        }
        var fnBody = fn.substring(fn.indexOf("{") + 1, fn.lastIndexOf("}"));
        var params = fn.substring(fn.indexOf("(") + 1, fn.indexOf(")"));
        try{
            func = new Function(params,fnBody);
        }catch(err){
            error = err;
        }

        if (!error.error)
            return func;
        else
            return error;//{error: {line: error.line, text: "MapSVG user function error: (line "+error.line+"): "+error.message}};
    }
    /**
     * Returns map options.
     * @param {bool} forTemplate - If options should be formatted for use in a template
     * @param {bool} forWeb - If options should be formatted for use in the web
     * @param {object} optionsDelta - used by backend WP admin only, part of options from the previous view mode
     * @returns {object}
     */
    getOptions(forTemplate: boolean, forWeb: boolean) {

        var _this = this;

        var options = <MapOptionsInterface>$.extend(true, {}, _this.options);

        $.extend(true, options, this.optionsDelta);

        options.viewBox = _this._viewBox.toArray();
        options.filtersSchema = _this.filtersSchema.getFieldsAsArray();
        if (options.filtersSchema.length > 0) {
            options.filtersSchema.forEach(function (field) {
                if (field.type === 'distance') {
                    field.value = '';
                }
            });
        }

        // @ts-ignore - old options field from MapSVG versions <5.x
        delete options.markers;

        if (forTemplate){
            // @ts-ignore - may this needs to be removed?
            options.svgFilename = options.source.split('/').pop();
            // @ts-ignore - may this needs to be removed?
            options.svgFiles = MapSVG.svgFiles;
        }

        if(forWeb)
            $.each(options,function(key,val){
                if(JSON.stringify(val) == JSON.stringify(_this.defaults[key]))
                    delete options[key];
            });
        delete options.backend;

        return options;
    }

    /**
     * Restore delta options, used by MapSVG-Admin
     * @private
     */
    restoreDeltaOptions(): void {
        this.update(this.optionsDelta);
        this.optionsDelta = {};
    }

    /**
     * Sets event handlers
     * @param {object} functions - Callbacks {eventName, eventName, ...}
     */
    setEvents(functions: {[key: string]: string|Function}){

        // TODO add namespaces for events as done in jQuery

        var _this = this;
        let compiledFunction;

        for (var eventName in functions) {

            if (typeof functions[eventName] === 'string') {
                compiledFunction = functions[eventName] != "" ? <Function>this.functionFromString(<string>functions[eventName]) : null;
                if (!compiledFunction || compiledFunction.error || (compiledFunction instanceof TypeError || compiledFunction instanceof SyntaxError)){
                    continue;
                }
            } else if(typeof functions[eventName] === 'function') {
                compiledFunction = <Function>functions[eventName];
            }

            // Reset events
            _this.events.off(eventName);
            // Set new event
            _this.events.on(eventName, compiledFunction);

            if(eventName.indexOf('directory')!==-1){
                var event = eventName.split('.')[0];

                if(_this.controllers && _this.controllers.directory){
                    _this.controllers.directory.events.off(event);
                    _this.controllers.directory.events.on(event, compiledFunction);
                }
            }

        }

        $.extend(true, _this.options.events, functions);
    }
